}
#Jl #name {
    color: #4792cf;
    font-weight: lighter;
    margin-top: 1.4rem;
    margin-bottom: 0.2rem;
    font-size: 4rem;
    letter-spacing: 0.3rem;
    margin-left: 2rem;
}
#Jl .icon {
    vertical-align: middle;
    fill: currentColor;
    overflow: hidden;
}
#Jl .title {
    font-size: 1.8rem;
    background: #4792cf;
    color: white;
    padding: 0.5rem 1.5rem;
    width: 14rem;
    margin-top: 0.8rem;
    margin-bottom: 0.8rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#Jl .title:nth-child(1) {
      margin-top: 0;
}
#Jl .title .icon {
      display: inline-block;
      width: 1.5rem;
      height: 1.5rem;
}
#Jl .title > span {
      margin-left: 1rem;
}
#Jl .maininfo {
    width: 85%;
    margin-left: 2rem;
    font-size: 1.5rem;
}
#Jl .maininfo td {
      padding: 0.3rem;
}
#Jl .maininfo td:nth-child(1) {
        width: 10%;
}
#Jl .maininfo td:nth-child(2) {
        width: 34%;
}
#Jl .maininfo td:nth-child(3) {
        width: 15%;
}
#Jl .sk {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-left: 5px;
    -webkit-box-flex: 2;
        -ms-flex-positive: 2;
            flex-grow: 2;
}
#Jl .sk > .sk-tab {
      -webkit-box-flex: 2;
          -ms-flex-positive: 2;
              flex-grow: 2;
}
#Jl .sk > .sk-tab > div {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
#Jl #me {
    position: absolute;
    top: 0;
    right: 1%;
    width: 15%;
}
#Jl .educate {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    font-weight: bold;
    font-size: 1.5rem;
}
#Jl .bold {
    font-weight: bold;
}

.InfoComp .head {
  font-weight: bold;
  font-size: 1.5rem;
  margin-left: 0%;
  width: 100%;
  padding-left: 0%;
  padding-right: 0%;
  padding-bottom: 0.3rem;
  margin-top: 1rem;
}
.InfoComp .head.headborder {
    border-bottom: 3px solid #63aeeb;
}
.InfoComp .head td {
    white-space: nowrap;
}
.InfoComp .head td:nth-child(1) {
      width: 40%;
}
.InfoComp .head td:nth-child(3) {
      text-align: right;
}
.InfoComp > div:nth-child(1) .head {
  margin-top: 0;
}
.InfoComp .contents {
  font-size: 1.4rem;
  padding: 0.3rem;
}
.InfoComp .contents > div {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0.1rem;
    margin-left: 1rem;
}
.InfoComp .contents > div .icon {
      width: 1.2rem;
      height: 1.2rem;
      color: #1e8ccb;
      margin-top: -0.4rem;
}
